import Vue from 'vue';
import VueRouter from 'vue-router';
import HomeView from '../views/HomeView.vue';
import AddTask from '@/views/AddTask.vue';
import TaskList from '@/views/TaskList.vue';
import CombinedView from '@/views/CombinedView.vue';
// Importa los otros componentes en esta secci√≥n

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },

  {
    path: '/addtask',
    name: 'AddTask', // Ruta para a√±adir tareas
    component: AddTask // Aqu√≠ se debe cargar el componente AddTask
    // Implementa la l√≥gica para a√±adir nuevas tareas.
  },

  {
    path: '/tasklist',
    name: 'TaskList', // Ruta para a√±adir tareas
    component: TaskList // Aqu√≠ se debe cargar el componente AddTask
    // Implementa la l√≥gica para a√±adir nuevas tareas.
  },
  {
    path: '/combinedview',
    name: 'CombinedView', // Ruta para a√±adir tareas
    component: CombinedView // Aqu√≠ se debe cargar el componente AddTask
    // Implementa la l√≥gica para a√±adir nuevas tareas.
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
});

export default router;
